:root{
  --bg-main: white;
  --bg-glass: rgba(255, 255, 255, 0.104);
  --text-sigma: black;
  --text-bright-text: rgb(103, 103, 103);
  --text-highlight-text: rgb(79, 79, 79);
  --border-shin: rgba(201, 184, 227, 0.476);
  --Theme-changing-button: "./Assets/Images/Theme-Dark.svg"
}

[App-theme="dark"]{
  --bg-main: rgb(45, 45, 45);
  --bg-glass: rgba(0, 0, 0, 0.104);
  --text-sigma: rgb(255, 255, 255);
  --text-bright-text: rgb(125, 125, 125);
  --text-highlight-text: rgb(168, 168, 168);
  --border-shin: rgba(68, 56, 87, 0.37);
  --Theme-changing-button: "./Assets/Images/Theme-Light.svg"
}

/* Components */

/* main body */

.mainBody {
  background-color: var(--bg-main);
  background-image: url("./Assets/Images/BG.png");
  background-size: 15px;
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  z-index: 1;
}


/* Toolbar */
.Tools {
  position: -webkit-sticky;
  bottom: 15px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  position: fixed;
  z-index: 1;
}

.Tools > div {
  background: rgba(154, 141, 175, 0.147);
  border-radius: 16px;
  box-shadow: 4px 4px 10px var(--border-shin);
  backdrop-filter: blur(2px);
  -webkit-backdrop-filter: blur(2px);
  margin: 10px;
  padding: 7px 15px 5px 15px;
}

.theme-button{
  cursor: pointer;
}

/* ContentBody */
.ContentBody{
  align-self: center;
  background: var(--bg-glass);
  backdrop-filter: blur(4.5px);
  -webkit-backdrop-filter: blur(4.5px);
  min-width: 400px;
  max-width: 1400px;
  height: fit-content;
  width: 80%;
  height: 90em;
  z-index: 0;
}

/* Header */
.Header{
  width: 100%;
  background-color: var(--bg-main);
  display: flex;
  flex-direction: row;
  padding: 10px 0;
}

.Header > img{
  margin-left: 15px;
  margin-right: auto;
}

.Header > h3{
  color: var(--text-highlight-text);
  margin-right: 15px;
}

/* Components */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
